.sidebarOption{
    display: flex;
    align-items: center;
    cursor: pointer;
    color: black;
    text-decoration: none;
}

.sidebarOption:hover{
    background-color: var(--twittie-hover);
    border-radius: 3rem;
    color: var(--twittie-color);
    transition: color 100ms ease-out;
}

.sidebar__active{
    color: var(--twittie-color);
}

.sidebarOption>h2{
    font-weight: 700;
    font-size: 1.9rem;
    margin-right: 2rem;
}

.sidebarOption > .MuiSvgIcon-root {
    font-size: 2.4rem;
    padding: 1.4rem 1.4rem 1.4rem 2rem;
}

@media screen and (max-width: 1282px){
    .sidebarOption>h2{
        display: none;
    }
    .sidebarOption > .MuiSvgIcon-root{
        margin-left: auto;
    }
    .sidebarOption{
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
    }
}
.sidebarAccount__wrapper{
    display: flex;
    flex-direction: row;
    border-radius: 3rem;
    align-items: center;
    margin-top: 15%;
}

.sidebarAccount__ava{
    border-radius: 50%;
    padding: 1rem;
}

.sidebarAccount__wrapper:hover{
    background-color: #e8f5fe;
}

.sidebarAccount__userData{
    display: flex;
    flex-direction: column;
    flex: 1 1;
}

.sidebarAccount__userData h2:nth-child(2){
    color: var(--twittie-light);
    font-weight: 450;
}

.sidebarAccount__expandIcon{
    margin-right: 1rem;
    font-size: 2rem;
}

.MuiPopover-paper  {
    border-radius: 2rem !important;
}

.logoutBtn{
    cursor: pointer;
}

@media screen and (max-width: 1282px){
    .sidebarAccount__userData, .sidebarAccount__expandIcon {
        display: none;
    } 
}
.sidebar{
    border-right: 1px solid var(--twittie-background);
    flex: 0.2 1;
    padding-top: 2rem;
    padding-left:2rem;
    padding-right:2rem;
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
}

.sidebar__twitterIcon{
    color: var(--twittie-color);
    font-size: 3rem !important;
    margin-left: 2rem;
    margin-bottom: 2rem;
    margin-top: -.3rem;
}

.sidebar__tweet{
    background-color: var(--twittie-color) !important;
    border: none !important;
    color: white !important;
    font-weight: 900 !important;
    text-transform: inherit !important;
    border-radius: 3rem !important;
    height: 5rem;
    margin-top: 2rem !important;

    width: 100%;
}

.sidebar__tweet>div{
    max-width: 3rem;
    margin-top: .3rem;
    margin-left: .3rem;
}


@media screen and (max-width: 1282px){
    .sidebar__tweet{
        /* display: none !important; */
        width: 5rem !important;
        /* border-radius: 50% !important; */

    }
    .sidebar{
        flex: unset !important;
    }
    .sidebar__tweet span{
        display: none;
    }
}


@media screen and (max-width: 500px){
    .sidebar{
        display: none;
    }

    .sidebar__tweet{
        margin-left: 1rem !important;
    }
}

@media screen and (min-width: 1282px){
    .sidebar__tweet>div{
        display: none
    }
}
.tweetBox {
    padding-bottom: 1rem;
    border-bottom: .8rem solid var(--twittie-background);
    padding-right: 1rem;
}

.tweetBox>form {
    display: flex;
    flex-direction: column;
}

.tweetBox__wrapperInput{
    display:flex;
    flex-direction:row
}

.tweetBox__ava{
    padding: 2rem;
}

.tweetBox__input {
    display:flex;
    flex-direction:column; 
    flex:1 1;
    margin-left: 1rem;    
}

.tweetBox__input-image{
    position: relative;
    margin-bottom: 1rem;
}

.tweetBox__input-image::after{
    display: block;
    content: '';
    padding-top: 56.25%;
}

.tweetBox__input-image>img{
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 2rem;

    opacity: 1; 
    transition: opacity 1.5s ease-out;
}

.tweetBox__input> input{
    flex: 1 1;
    border: none;
    font-size: 1.6rem;
    outline: none;
    padding: 3rem 0 2rem 0;
}

.tweetBox__input>textarea{
    overflow:hidden;
    padding: 3rem 0 2rem 0;
    max-width: 100%;
    font-size:1.6rem;
    resize: none;
    display:flex;
    border: none;
    outline: none;
    font-family: inherit !important;
    font-weight: 499 !important;
    color: inherit !important;
}

.tweetBox__imageInput{
    border: none;
    padding: 1rem;
}

.tweetBox__tweetButton{
    background-color: var(--twittie-color) !important;
    border: none !important;
    color: white !important;
    font-weight: 900 !important;
    text-transform: inherit !important;
    border-radius: 3rem !important;
    height: 4rem;
    width: 8rem;
    margin-top: 2rem !important;  
    margin-left: auto !important;
}

.cancelIcon{
    font-size: 3.5rem !important;
    position: absolute;
    top: .5rem;
    left: .5rem;
    z-index: 2;
    cursor: pointer !important;
}

.cancelIcon:hover{
    opacity: 0.6;    
}

.editImage{
    background-color: black !important;
    border: none !important;
    color: white !important;
    font-weight: 500 !important;
    text-transform: inherit !important;
    border-radius: 3rem !important;
    height: 3rem;
    position: absolute !important;
    right: .8rem;
    top: .7rem;
    font-size: 1.6rem !important;
}

.editImage:hover{
    opacity: 0.6;
}

.tweetBox__input-actions{
    display:flex !important;
    flex-direction:row !important;
}

.tweetBox__input-icons{
    display: flex;
    flex-direction: row;
    align-items: center;
    flex:1 1;
    margin-left: -.8rem;
}

.tweetBox__input-icons>div{
    padding: .6rem;
    border-radius: 50%;
    cursor: pointer;
}

.tweetBox__input-icons>div:nth-child(3){
    transform: rotate(90deg);
}

.tweetBox__input-icons>div:hover{
    background-color: #f5f8fa;
}

.tweetBox__input-icons .MuiSvgIcon-root{
    font-size: 2rem;
    color: var(--twittie-color);
    cursor: pointer;
}

